@import "./utils/_normalize.scss";

//page
* {
  box-sizing: border-box;
}

.page__body {
  min-width: 320px;

  font-family: Poppins, sans-serif;
}

//menu
.page__menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  transition: all .5s;
  transform: translateX(-100%);
  pointer-events: none;
  opacity: 0;

  &:target {
    opacity: 1;
    pointer-events: all;
    transform: translateX(0);
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  background: #2c2c2c;
  font-weight: 700;
  overflow: auto;
  position: relative;

  &__content {
    padding: 52px 16px;
  }

  &__item {
    color: #fff;
  }
}

.close--platform {
  display: flex;
  float: right;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 8px;
  position: sticky;
  top: 47px;
  right: 33px;
  background-color: #474747;
}

.close__link {
  display: flex;
}


//nav
.nav {
  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__list--for-header {
    display: flex;
    align-items: center;
  }

  &__list--for-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 30px;
  }

  &__item {
    &--spaced {
      margin-right: 48px;
    }
  }


  &__link {
    display: flex;
    text-decoration: none;
    color: #fff;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 150%;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__link--for-header {
    @media (max-width: 640px) {
      display: none;
    }

    @media (min-width: 1024px) {
      &:hover {
        color: #2060f6;
      }
    }

    transition: color .3s ease-out;
  }
}

.hire-us__link {
  transition: color .3s ease-out;
  text-decoration: none;
  color: #fff;
  text-align: center;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  letter-spacing: 3px;
  text-transform: uppercase;
  position: relative;

  @media (min-width: 1024px) {
    &:hover {
      color: #2060f6;
    }
  }

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 2px;
    background-color: #2060f6;
    border-radius: 4px;
    pointer-events: none;
    position: absolute;
    bottom: -8px;
  }
}


//header
.header {
  height: 100vh;
  width: 100%;
  position: relative;

  &__welcome {
    position: relative;
  }

  &__main {
    background-image: url(../images/header/bg-black-header.png);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    top: 0;
    border-radius: 0 0 30px 30px;
    height: 800px;
  }

  &__logo {
    display: flex;
    margin-right: 32px;
    transition: transform .3s linear;

    @media (min-width: 1024px) {
      &:hover {
        transform: scale(1.08);
      }

      margin-right: 0;
    }
  }

  &__top-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__left {
    display: flex;
  }

  &__button {
    display: flex;
    border: none;
    background-color: transparent;

    @media (min-width: 641px) {
      display: none;
    }
  }

  &__title {
    color: #fff;
    font-family: inherit;
    font-size: 64px;
    font-weight: 600;
    line-height: 68px;
    letter-spacing: 0px;
    margin-top: 168px;

    @media (min-width: 800px) {
      width: 490px;
    }
  }

  &__subtitle {
    color: #fff;
    font-family: Open Sans;
    font-size: 18px;
    font-weight: 400;
    line-height: 150%;
    width: 362px;
    margin-top: 32px;

    @media (max-width: 480px) {
      width: 362px;
    }
  }

  &__lm-button {
    // display: flex;
    // justify-content: center;
    // align-items: center;
    display: inline-block;
    vertical-align: middle;
    text-align: center;
    text-decoration: none;
    // padding: 14px 88px 13px 89px;
    width: 264px;
    height: 50px;
    border-radius: 8px;
    background-color: #2060f6;
    border: none;
    color: #fff;
    font-family: inherit;
    font-size: 15px;
    font-weight: 600;
    user-select: none;
    transition: opacity .3s linear, box-shadow .3s linear;

    &--spaced {
      margin-top: 82px;
    }

    &:hover {
      @media (min-width: 1024px) {
        opacity: 0.87;
        box-shadow: 0 0.5rem 2rem rgba(#2060f6, .175);;
      }
    }
  }
}

.slider {
  background: url(../images/header/slider/1.png), lightgray 50% / cover no-repeat;
  border-radius: 30px 0px;
  background-position: center;
  height: 687px;
  width: 680px;
  position: absolute;
  bottom: 0;
  right: 0;

  &__wrapper {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__content {
    display: flex;
    gap: 24px;
    position: absolute;
    left: 16px;
    bottom: 48px;
  }

  &__ellipses {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
  }

  &__description {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__title {
    color: #fff;
    font-family: Poppins, sans-serif;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__subtitle {
    color: #fff;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
  }
}

.ellipse-4 {
  background-image: url(../images/header/slider/ellipse-4.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 32px;
  height: 32px;
  position: relative;
  transition: transform .2s linear;

  &:hover {
    transform: scale(1.2);
  }

  &--left {
    // background-image: url(../images/header/slider/ellipse-4.svg);
    // background-color: #fff;
    // border-radius: 50%;
    display: inline-block;
    width: 32px;
    height: 32px;
  }

  &--right {
    // background-image: url(../images/header/slider/ellipse-4.svg);
    // background-color: #fff;
    // border-radius: 50%;
    display: inline-block;
    width: 32px;
    height: 32px;
    float: right;
  }
}

.mdi_chevron_left {
  background-image: url(../images/header/slider/mdi_chevron_left.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 18px;
  height: 18px;
}

.mdi_chevron_right {
  background-image: url(../images/header/slider/mdi_chevron_right.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 18px;
  height: 18px;
}


//container
.container {
  margin-inline: auto;
  padding-inline: 54px;
  padding-top: 48px;
  max-width: 1024px;
}

.welcome {
  height: 1280px;
  width: 100%;
  background-color: #f2f6fA;
  position: absolute;
  top: 0;
  z-index: -100;
  display: flex;
  justify-content: center;
  align-items: flex-end;

  &__content {
    text-align: center;
    color: var(--Text-100, #253757);
    width: 754px;
    margin-bottom: 148px;
  }

  &__title {
    color: var(--Text-100, #253757);
    // text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-weight: 600;
    line-height: 150%;
  }

  &__paragraph {
    color: #6C788B;
    // text-align: center;
    font-family: "Open Sans";
    font-size: 24px;
    font-weight: 600;
    line-height: 150%;
  }
}

.what-we-do {
  &__container {
    max-width: 1024px;
    margin-inline: auto;
    margin-top: 64px;
    display: grid;
    grid-template-columns: repeat(3, 214px);
    gap: 177px;
    justify-content: center;

    @media (max-width: 1024px) {
      gap: 137px;
    }

    @media (max-width: 640px) {
      grid-template-columns: repeat(1, 214px);
      // gap: 0;
    }
  }

  &__title {
    color: var(--Text-100, #253757);
    text-align: center;
    font-family: inherit;
    font-family: serif;
    font-size: 52px;
    font-weight: 600;
    line-height: 150%;
  }

  &__item {
    width: 214px;
  }
}

.item {
  &__image {
    margin-inline: auto;
    width: 184px;
    height: 166px;
    border-radius: 20px;
  }

  &__title {
    color: var(--Text-100, #253757);
    text-align: center;
    font-family: inherit;
    font-size: 20px;
    font-weight: 600;
    line-height: 140%;
    margin-top: 40px;
  }

  &__paragraph {
    color: #6C788B;
    text-align: center;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 150%;
    margin-top: 16px;
  }
}
